import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";

@Injectable({
  providedIn: 'root'
})
export class VaccineService {
  districtsAPI = 'https://cdn-api.co-vin.in/api/v2/admin/location/districts/';
  vaccineAPI = 'https://cdn-api.co-vin.in/api/v2/appointment/sessions/public/calendarByDistrict?district_id=';
  constructor(private http: HttpClient) {
  }

  getDistrict(stateId: number) {
    return this.http.get(this.districtsAPI + stateId.toString());
  }

  getVaccineInfoForNext7Days( districtId: number) {
     let date  = new Date();
    let today = date.getDate() + '-' + (date.getMonth() + 1) +'-' + date.getFullYear();
    return this.http.get(this.vaccineAPI + districtId + '&date=' + today);
  }
}
