import {Component, OnInit} from '@angular/core';
import {State} from "../models/state.model";
import {States} from "../static-data/states";
import {District} from "../models/district.model";
import {Vaccine} from "../models/vaccineinfo.model";
import {VaccineService} from "../services/vaccine.service";
import {animate, state, style, transition, trigger} from "@angular/animations";

@Component({
    selector: 'app-vaccine-slot',
    templateUrl: './vaccine-slot.component.html',
    styleUrls: ['./vaccine-slot.component.css'],
    animations: [
        trigger('vaccineinfo', [
            state('normal', style({
                'color':'red !important'
            })),
            state('in', style({
                'color':'red !important'
            })),

            transition('void => normal ', animate(2000))
        ])

    ]
})
export class VaccineSlotComponent implements OnInit {

    states: State[] = new States().statesInfo;
    mockDistrict: District = {
        'district_id': 0,
        'district_name': 'Select Your District'
    };
    displayIndex: number;
    districts: District[] = [this.mockDistrict];
    selectedstate = 0;
    selecteddistrict = 0;
    vaccineInfo: Vaccine[];
    vaccineInfoResponse: Vaccine[];
    filteredValues: Vaccine[];
    loading = false;
    paginate: { prevBtn: boolean, nextBtn: boolean } = {prevBtn:false, nextBtn: true};
    filteredAge= 'ALL';
    filteredVaccineType= 'ALL';

    constructor(private vaccineService: VaccineService) {
    }

    ngOnInit() {
    }

    fetchDistricts() {
        this.districts = [this.mockDistrict];
        if (this.selectedstate > 0) {
            this.vaccineService.getDistrict(this.selectedstate).subscribe((result: { districts: District[], ttl: string; }) => {
                for (const res of result.districts) {
                    this.districts.push(res);
                }
                this.selecteddistrict = 0;
            });
            return;
        } else {
            this.selecteddistrict = 0;
            this.selectedstate = 0;
            this.vaccineInfo = [];
        }
    }

    getVaccineInfo() {
        this.loading = true;
        if (this.selecteddistrict > 0) {
            this.vaccineService.getVaccineInfoForNext7Days(this.selecteddistrict).subscribe((result: { centers: Vaccine[] }) => {
                this.vaccineInfoResponse = result.centers.filter(item => item.sessions[0].available_capacity > 0);
                this.filteredValues = this.vaccineInfoResponse;
                this.vaccineInfo = this.filteredValues.slice(0, 10);
                this.initializeResults(this.filteredValues);
            });
        }

    }


    nextTen() {
        this.vaccineInfo = this.filteredValues.slice(this.displayIndex, this.displayIndex + 10);
        if (this.displayIndex+10 >= this.filteredValues.length)  {
            this.paginate.nextBtn = true;
            this.displayIndex += 10;
            this.paginate.prevBtn = false;
        } else {
            this.paginate.nextBtn = false;
            this.displayIndex += 10;
            this.paginate.prevBtn = false;
        }


    }

    prevTen() {
        const prevNumber= this.displayIndex - 20;
        this.vaccineInfo = this.filteredValues.slice(prevNumber, this.displayIndex-10);
        this.paginate.nextBtn = false;
        this.displayIndex -= 10;
        if (this.displayIndex <= 10) {
            this.paginate.prevBtn=true;
        }
    }

    filterResults() {
        let filteredResults: Vaccine[] = this.vaccineInfoResponse;
        if (this.filteredAge === 'ALL' && this.filteredVaccineType === 'ALL') {
            filteredResults = this.vaccineInfoResponse;
        }
        if (this.filteredAge !=='ALL'){
            filteredResults = this.vaccineInfoResponse.filter(item=> item.sessions[0].min_age_limit.toString() === this.filteredAge);
        }
        if (this.filteredVaccineType !== 'ALL') {
            filteredResults = filteredResults.filter( item=> item.sessions[0].vaccine===this.filteredVaccineType);
        }
        this.filteredValues = filteredResults;
        this.vaccineInfo = this.filteredValues.slice(0, 10);
        this.initializeResults(filteredResults);



    }

    initializeResults(results: Vaccine[]){

        this.displayIndex = 10;
        if (this.vaccineInfo.length === results.length || this.vaccineInfo.length < 10) {
            this.paginate.prevBtn = true;
            this.paginate.nextBtn = true;
        }else {
            this.paginate.prevBtn = true;
            this.paginate.nextBtn = false;
        }
        this.loading = false;
    }
}
